import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validasiSaldoAwalPiutang from "../../Validations/validasiSaldoAwalPiutang";
import { renderField } from "../../renderField";
import { getListBarang } from "../../actions/kodeBarangAction";

class FormSaldoAwalStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      berat : "Berat (Gr)"
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }
  setKodeBarang(data) {
    var barang = data.target.value || "DEFAULT|DEFAULT|DEFAULT";
    var split = barang.split("|");
    this.props.change("kadar", split[2]);
    if(split[3] === "AKSESORIS"){
      this.setState({
        berat : "Qty"
      })
    }
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <label htmlFor="">Kode Model</label>
            <Field
              name="kode_jenis"
              component="select"
              className="form-control"
              onChange={(data) => this.setKodeBarang(data)}
            >
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.props.KodeBarangList.filter(
                (list) => list.kode_kelompok === "PERHIASAN" || list.kode_kelompok === "AKSESORIS"
              ).map((list) => (
                <option
                  value={
                    list.kode_jenis +
                    "|" +
                    list.nama_jenis +
                    "|" +
                    list.kadar_cetak +
                    "|" +
                    list.kode_kelompok
                  }
                  key={list.kode_jenis}
                >
                  {list.nama_jenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-lg-3">
            <Field
              label="Kode Tukar"
              name="kadar"
              component={renderField}
              type="number"
              readOnly={this.state.berat === "Qty"}
            />
          </div>
          <div className="col-lg-3">
            <Field
              label={this.state.berat}
              name="bruto"
              component={renderField}
              type="number"
            />
          </div>
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button className="btn btn-primary" type="submit">
              Selesai
              <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalStok = reduxForm({
  form: "FormSaldoAwalStok",
  enableReinitialize: true,
  validate: validasiSaldoAwalPiutang,
})(FormSaldoAwalStok);
export default connect((state) => {
  return {
    KodeBarangList: state.provinsi.kodeBarang,
  };
})(FormSaldoAwalStok);
