import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { berhasil, gagal } from "../Alert";
import FormSaldoAwalStok from "./Component/FormSaldoAwalStok";
import { getListDataSaldoAwalStok } from "../actions/transaksuPenjualanLMAction";
import Loader from "react-loader-spinner";
import { Link } from "react-router-dom";
import HeaderCard from "../HeaderCard";
import { getLokasi } from "../actions/lokasiAction";
import { renderFieldSelect } from "../renderField";
import { getListBarang } from "../actions/kodeBarangAction";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { AxiosMasterPost } from "../Axios";
import { finishSend, progressSend } from "../actions/LogicAction";
import ReactTooltip from "react-tooltip";
import Swal from "sweetalert2";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    dataSaldoAwalStok: state.provinsi.dataSaldoAwalStok,
    lokasi: state.provinsi.lokasi,
    onSend: state.provinsi.onSend,
  };
};
class SaldoAwalStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tipeTransksi: "HUTANG",
      listSales: [],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Jenis",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar ",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "bruto",
          text: "Berat",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, row.kode_jenis)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      tanggal: "",
      sales: "",
      loading: false,
    };
  }
  sendSaldoAwal() {
    if (this.state.tanggal === "") {
      gagal("Mohon isi tanggal");
      return false;
    } else if (this.state.kode_lokasi === "") {
      gagal("Mohon isi Lokasi");
      return false;
    } else if (localStorage.getItem("SaldoAwalStock") === null) {
      gagal("Data masih Kosong, Silahkan tambahkan dahulu");
      return false;
    }
    this.props.dispatch(progressSend());
    let data = {
      no_transaksi: this.props.noFaktur,
      jenis_trx: "STOCK",
      tanggal: this.state.tanggal,
      kode_lokasi: this.state.kode_lokasi,
      detail_saldo_awal: JSON.parse(localStorage.getItem("SaldoAwalStock")),
    };
    AxiosMasterPost("saldo-awal/simpan", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => berhasil("Saldo Awal Berhasil Disimpan"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => localStorage.removeItem("SaldoAwalStock"))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.props.dispatch(getListDataSaldoAwalStok()))
      .catch(() =>
        gagal("Sepertinya ada gangguan, Mohon periksa koneksi anda").then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  deleteRow(rowIndex, dihapus) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array = JSON.parse(localStorage.getItem("SaldoAwalStock")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("SaldoAwalStock", JSON.stringify(array));
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        }).then(() => this.props.dispatch(getListDataSaldoAwalStok()));
      }
    });
  }
  setTipe(e) {
    this.setState({
      tipeTransksi: e.target.value,
    });
    this.props.dispatch(reset("FormSaldoAwalPiutang"));
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
    this.props.dispatch(getListDataSaldoAwalStok());
    this.props.dispatch(getLokasi());
    this.props.dispatch(getListBarang());
  }
  handleChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handlerSimpan(data) {
    var isi = {
      kode_supplier: "-",
      kode_customer: "-",
      kode_sales: "-",
      no_bon: "-",
      saldo_gr: 0,
      saldo_rp: 0,
      titip_gr: 0,
      titip_rp: 0,
      kode_jenis: data.kode_jenis.split("|")[0],
      kadar_cetak: data.kode_jenis.split("|")[2].toString(),
      bruto: data.bruto,
      netto: 0,
    };
    console.log(JSON.stringify(isi));
    var hasil = JSON.parse(localStorage.getItem("SaldoAwalStock")) || [];
    hasil.push(isi);
    localStorage.setItem("SaldoAwalStock", JSON.stringify(hasil));
    berhasil("Data Tersimpan");
    this.props.dispatch(getListDataSaldoAwalStok());
  }
  render() {
    if (this.state.loading === true) {
      return (
        <div
          className="d-flex no-block justify-content-center align-items-center position-relative"
          style={{ width: "100%", height: "90vh" }}
        >
          <div>
            <Loader type="Oval" color="#00BFFF" height={70} width={70} />
          </div>
        </div>
      );
    } else {
      return (
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/saldoawal">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="row">
            <div className="col-lg-12">
              <div className="card">
                <HeaderCard title="Saldo Awal Stock" />
                <div className="card-body">
                  <div className="col-lg-12 mt-3">
                    <div className="row">
                      <div className="col-lg-4">
                        <label htmlFor="">Tanggal</label>
                        <input
                          type="date"
                          className="form-control"
                          onChange={this.handleChange("tanggal")}
                        />
                      </div>
                      <div className="col-lg-4">
                        <Field
                          name="kode_lokasi"
                          component={renderFieldSelect}
                          options={this.props.lokasi.map((list) => {
                            let data = {
                              value: list.kode_lokasi,
                              name: list.nama_lokasi,
                            };
                            return data;
                          })}
                          type="text"
                          label="Kode Lokasi"
                          placeholder="Masukan Kode Lokasi"
                          onChange={(data) =>
                            this.setState({
                              kode_lokasi: data,
                            })
                          }
                        />
                      </div>
                      <div className="col-lg-12 mt-3">
                        <div className="row">
                          <div className="col-lg-6"></div>
                          <div className="col-lg-6">
                            <div className="text-right">
                              <button
                                className="btn btn-primary"
                                data-target="#tambahBarang"
                                data-toggle="modal"
                              >
                                Tambah Data
                              </button>
                            </div>
                          </div>
                          <div className="col-lg-12 mt-3">
                            <GlobalTabel
                              keyField="kode_jenis"
                              data={this.props.dataSaldoAwalStok}
                              columns={this.state.columns}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <div className="text-right">
                          <button
                            type="submit"
                            className="btn btn-info"
                            disabled={this.props.onSend}
                            onClick={() => this.sendSaldoAwal()}
                          >
                            {this.props.onSend ? (
                              <span
                                className="spinner-border spinner-border-sm mr-3"
                                aria-hidden="true"
                              ></span>
                            ) : null}
                            Simpan
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            id="tambahBarang"
            className="modal fade"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="primary-header-modalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header modal-colored-header bg-primary">
                  <h4 className="modal-title" id="primary-header-modalLabel">
                    Tambah Data Transaksi
                  </h4>
                  <button
                    id="dismiss_modal1"
                    name="dismiss_modal1"
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >
                    ×
                  </button>
                </div>
                <div className="modal-body">
                  <FormSaldoAwalStok
                    onSubmit={(data) => this.handlerSimpan(data)}
                    tipe={this.state.tipeTransksi}
                  />
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-light"
                    data-dismiss="modal"
                    id="close_modal"
                  >
                    Close
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}

SaldoAwalStok = reduxForm({
  form: "SaldoAwalStok",
  enableReinitialize: true,
})(SaldoAwalStok);
export default connect(maptostate, null)(SaldoAwalStok);
