const validasiSaldoAwalPiutang = (value) => {
  const errors = {};
  if (!value.kode_customer) {
    errors.kode_customer = "Tidak Boleh kosong";
  }
  if (!value.no_bon) {
    errors.no_bon = "Tidak Boleh kosong";
  }
  if (!value.bruto) {
    errors.bruto = "Tidak Boleh kosong";
  }
  if (!value.netto) {
    errors.netto = "Tidak Boleh kosong";
  }
  if (!value.saldo_gr) {
    errors.saldo_gr = "Tidak Boleh kosong";
  }
  if (!value.saldo_rp) {
    errors.saldo_rp = "Tidak Boleh kosong";
  }
  if (!value.titip_gr) {
    errors.titip_gr = "Tidak Boleh kosong";
  }
  if (!value.titip_rp) {
    errors.titip_rp = "Tidak Boleh kosong";
  }
  return errors;
};

export default validasiSaldoAwalPiutang;
